$theme: (
    green-theme: (color: #27ea60,),
    blue-theme: (color: #2980b9),
    red-theme: (color: #c0392b),
    gray-theme: (color: #2c3e50),
);

@each $theme, $map in $theme {
    .#{$theme} {
        .page {
            .page-head  {
                .page-title{
                    margin-top: 20px;
                    color: map-get($map , color)
                }
            }
        }
    }
}