.header {
    background-color: #FFF;
    padding: 10px;
    i {
        cursor: pointer;
        width: 5%;
    }
    .head-area {
        display: flex;
        .search {
            display: flex;
            width: 300px;
            position: relative;
            .toggle-button {
                width: 10%;
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
            }
            input {
                width: 100%;
                padding: 8px 15px;
                background-color: $content-color;
                border: none;
                outline: none;
                border-radius: 30px;
                margin-left: 10%;
            }
            .fa.fa-search  {
                position: absolute;
                z-index: 2;
                right: 15px;
                top: 8px;
                color: #999;
            }
        }
        .icons {
            width: calc(100% - 300px);
            position: relative;
            z-index: 5;
            ul {
                position: absolute;
                right: 0px;
                top: 50%;
                transform: translateY(-50%);
                display: flex;
                li {
                    margin-left: 15px;
                    margin-right: 15px;
                    img {
                        height: 30px;
                        border-radius: 50%;
                        cursor: pointer;
                    }
                }
            }
            
        }
    }
}