.setting-box {
    width: 250px;
    height: 100vh;
    position: fixed;
    right: 0;
    top: 0;
    background-color: #FFF;
    z-index: 3;
    transition: .3s;
    border: 1px solid #EEE;
    &.hide-box {
        right: -250px;
    }
    .toggle-box {
        position: absolute;
        left: -34px;
        top: 100px;
        background-color: #FFF;
        padding: 5px 10px;
        cursor: pointer;
    }
    .settings-content {
        .wellcome {
            border-bottom: 1px solid #DDD;
            padding: 10px 5px;
            h2 {
                font-size: 14px;
                margin: 0;
                color: #333;
                font-weight: bold;
            }
        }
        .option-box {
            margin-bottom: 5px;
            border-bottom: 1px solid #DDD;
            padding: 10px 5px;
            h4 {
                text-transform: uppercase;
                margin-bottom: 5px;
                font-size: 14px;
                font-weight: bold;
                color: #444;
            }
            ul {
                margin: 0px;
                li {
                    margin-bottom: 0px !important;
                }
            }
        }
        .color-options {
            ul {
                $colors: #27ea60, #2980b9, #c0392b, #2c3e50;
                @for $i from 1 through length($colors) {
                    li:nth-child(#{$i}) {
                        background-color: nth($colors, $i);
                    }
                }
                li {
                    width: 20px;
                    height: 20px;
                    margin-bottom: 10px;
                    text-align: center;
                    padding: 5px;
                    color: #FFF;
                    cursor: pointer;
                    opacity: .7;
                    display: inline-block;
                    &.active {
                        opacity: 1;
                    }
                }
            }
        }
        .direction-option {
            .rtl {
                text-align: center;
                color: #FFF;
                padding: 5px;
                background-color: #e91e63;
                margin-bottom: 5px;
                border-radius: 5px;
                cursor: pointer;
                transition: .3s;
                opacity: .8;
                &:hover {
                    opacity: 1;
                }
            }
            .ltr {
                text-align: center;
                color: #FFF;
                padding: 5px;
                border-radius: 5px;
                background-color: #007bff;
                cursor: pointer;
                transition: .3s;
                opacity: .8;
                &:hover {
                    opacity: 1;
                }
            }
            
        }
        .theme-option {
            .themes {
                width: 90%;
                margin: auto;
                div {
                    display: inline-block;
                    padding: 5px;
                    border: 1px solid #EEE;
                    color: #666;
                    background-color: #f8f9fa;
                    cursor: pointer;
                    &.active {
                    border: 1px solid #999;
                    }
                }
            }
        }
        .slider-option {
            ul {
                li {
                    display: flex;
                    font-size: 14px;
                    text-transform: none;
                    i {
                        margin-left: auto;
                        position: relative;
                        top: 4px;
                        right: 0;
                        cursor: pointer;
                        font-size: 18px;
                    }
                }
            }
        }
    }
}