
.bg-primary-gradient {
    background-image: linear-gradient(to left, #0db2de 0%, #005bea 100%) !important;
}
.bg-danger-gradient {
    background-image: linear-gradient(45deg, #f93a5a, #f7778c) !important;
}

.bg-success-gradient {
    background-image: linear-gradient(to left, #48d6a8 0%, #029666 100%) !important;
}

.bg-warning-gradient {
    background-image: linear-gradient(to left, #efa65f, #f76a2d) !important;
}

.seles-card {
    padding: 15px 12px;
    color: #FFF;
    .title {
        text-transform: uppercase;
        font-size: 14px;
    }
    .static{
        & div:nth-child(1) {
            p {
                margin: 0;
            }
            span {
                font-size: 12px;
                opacity: .7;
            }
        }
        & div:last-child {
            margin-left: auto;
            font-size: 14px;
            color: #FFF;
            i {
                display: inline-block;
                margin: 5px;
            }
        }
    }
}

.card-title {
    margin-bottom: 0;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: bold;
}
.cadr-description {
    color: #7987a1  !important;
    font-size: 12px;
}

.card-head {
    padding: 10px 15px;
}

.card {
    margin: 1rem 0;
    border-radius: 10px;
    box-shadow: 0px 12px 18px 0 #dadee8;
}

.one-card {
    ul {
        li {
            padding: 15px;
            transition: .3s;
            .name {
                font-size: 15px;
                text-transform: capitalize;
            }
            .desc {
                font-size: 11px;
                color: #777;
            }
            &:hover {
                background-color: #ecf0fa;
                color: #0160e4;
            }
            .number {
            margin-left: auto;
            position: relative;
            top: 10px;
            font-size: 12px; 
            font-weight: bold;
        }
        }
    }
}

.sales {
        ul {
            li {
                .number {
                margin-left: auto;
                position: relative;
                top: 10px;
                font-size: 10px;
                color: #777; 
            }
            }
        }
}

.orders {
    .circle {
        width: 100px;
        height: 100px;
        border:2px dashed #28a745;
        border-radius: 50%;
        margin: 20px auto;
        position: relative;
        .num {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }
    .details {
        padding: 10px;
        display: flex;
        justify-content: space-around;
        .info {
            h6 {
                font-size: 14px;
                position: relative;
                &::before {
                    content: '';
                    position: absolute;
                    width: 7px;
                    height: 7px;
                    background-color: #0162e8;
                    left: -15px;
                    top: 4px;
                }
                &:last-child::before {
                    background-color: #00b9ff;
                }
            }
            h5 {
                margin: 0;
                font-weight: bold;
            }
            span {
                font-size: 12px;
                color: #777;
            }
        }
    }
}

.detail {
    padding: 10px;
    display: flex;
    justify-content: space-between;
    .info {
        flex: 1;
        h6 {
            font-size: 14px;
            position: relative;
        }
        h5 {
            margin: 0;
            font-weight: bold;
        }
        div {
            margin-top: 10px;
            background-color: #EEE;
            display: block;
            width: 90%;
            height: 4px;
            position: relative;
            span {
                position: absolute;
                left: 0;
                top: 0;
                width: 80%;
                height: 4px;
                &::before{
                    content: '';
                    position: absolute;
                    width: 15px;
                    height: 15px;
                    right: 0;
                    top: -6px;
                    background-color: #FFF;
                    border: 4px solid blue;
                    border-radius: 50%;
                }
                &.red::before {
                    border: 4px solid red;
                }
            }
        }
    }
}

.country {
    ul {
        li {
            padding: 3px 15px;
            border-bottom: 1px solid #EEE;
            &:hover {
                color: #333 ;
                background-color: #FFF ;
            }
            .name {
                position: relative;
                top: 10px;
            }
        }
    }
}