.page {
    .sidebar.black {
        background-color: #1f2940;
        color: #dadde2;
        &::-webkit-scrollbar {
            background-color: #081e3e;
        }
        &::-webkit-scrollbar-thumb {
            background-color: #081e3e;
        }
        &:hover::-webkit-scrollbar-thumb {
            background-color: #778395;
        }
        .logo {
            color: #778395;
            border-color: #778395;
        }
        .user-info {
            .img {
                box-shadow: none;
            }
        }
        .main .section .content .links>li {
            color: #778395;
            &:hover {
            color: #FFF;
            }
        }    
    }
}


.page {
    .sidebar.blue {
        background: linear-gradient(to right bottom, #005bea 0%, #0db2de 100%) !important;
        color: whitesmoke;
        &::-webkit-scrollbar {
            background-color: none;
        }
        &::-webkit-scrollbar-thumb {
            background-color: #EEE;
        }
        &:hover::-webkit-scrollbar-thumb {
            background-color: #DDD;
        }
        .logo {
            color: whitesmoke;
            border-color: #DDD;
        }
        .user-info {
            .img {
                box-shadow: none;
            }
            span {
                color: #FFF;
                opacity: .6;
                font-size: 12px;
            }
        }
        .main .section .content .links > li {
            color: whitesmoke;
            &:hover {
            color: #00c8ff;
            }
        }    
    }
}


.header.dark{
    background-color: #1f2940;
    .head-area {
        i {
            color: #FFF;
            opacity: .8;
        }
    }
}