.custom-drop{
    position: relative;
    .dropdown {
        background-color: #FFF;
        .dropdown-menu {
            padding: 0;
            border-radius: 6px;
            min-width: 240px;
            border-radius: 8px !important;
            .header {
                padding: 15px;
                background-color: #0162e8;
                color: #FFF;
                display: flex;
                border-top-right-radius: 5px !important;
                border-top-left-radius: 5px !important;
                img {
                    height: 40px;
                    width: 40px;
                    border-radius: 50%;
                    cursor: pointer;
                    background-color: red;
                }
                .info {
                    padding: 0 15px;
                    h3 {
                        font-size: 14px;
                        margin: 0;
                        font-weight: bold;
                    }
                    P {
                        margin: 0;
                        opacity: .7;
                    }
                }
            }
            .setting {
                .iconx {
                    font-size: 16px;
                    padding: 8px;
                    border-bottom: 1px solid #DDD;
                    display: flex;
                    flex-direction: row;
                    .set-icon{
                        margin-right: 20px;
                        width: 24px;
                        text-align: center;
                        display: block;
                        & > .dropdown-item:hover {
                            background-color: #FFF;
                        }
                    }
                    span {
                        font-weight: normal;
                        padding: 0 10px;
                        flex: 1;
                        width: 150px;
                    }
                }
            }
        }
    }
}