*{
    box-sizing: border-box;
}

.show {
    display: block !important;
}

body {
    font-family: sans-serif;
}

.page {
    .content-area {
        position: relative;
        z-index: 1;
        min-height: 100vh;
        width: calc(100% - 200px);
        transition: .3s;
        float: right;
        will-change: width;
        transition: .3s;
        background-color: $content-color;
        .content {
            margin-bottom: 30px;
        }
        &.nosidebar{
            width: calc(100% - 50px);
        }
        .page-head  {
            .page-title{
                margin-top: 20px;
                font-size: 16px;
                font-weight: bold;
                margin-bottom: 0;
            }
            .page-desc {
                font-size: 14px;
                color: #666;
                margin-top: 0px;
            }
        }
    }
}