.page {
    .sidebar {
        position: fixed;
        left: 0;
        top: 0;
        width: 200px;
        height: 100%;
        transition: .3s;
        overflow-y: auto;
        scroll-behavior: smooth;
        scrollbar-width: 5px;
        background-color: #FFF;
        &.nosidebar{
            left: -200px;
        }
        &::-webkit-scrollbar {
            display: block;
            background-color: #FFF;
            width: 3px;
        }

        &::-webkit-scrollbar-thumb {
            background-color: #FFF;
        }
        
        &:hover::-webkit-scrollbar-thumb {
            background-color: gainsboro;
            margin: 1px;
            border-radius: 10px;
        }
        .logo {
            padding: 15px;
            border-bottom: 1px solid $content-color;
            color: #00bcd4;
            font-weight: bold;
        }

        .user-info {
            text-align: center;
            padding: 15px;
            .img {
                width: 90px;
                height: 90px;
                border-radius: 50%;
                margin: auto;
                overflow: hidden;
                box-shadow: 0px 0px 5px 2px #DDD;
                img {
                    max-width: 100%;
                    height: 100%;
                }
            }
            .name {
                margin-bottom: 0;
                margin-top: 10px;
                font-size: 14px;
                font-weight: bold;
            }

            span {
                color: #999;
                font-size: 12px;
            }
        }

        .main {
            .section {
                .name {
                    font-weight: bold;
                    font-size: 12px;
                    padding-left: 20px;
                    padding-right: 20px;
                    text-transform: uppercase;
                }
                .content {
                    .links > li{
                        font-size: 12px;
                        margin-bottom: 5px;
                        transition: .3s;
                        cursor: pointer;
                        color: #777;
                        position: relative;
                        a {
                            display: block;
                            width: 100%;
                            padding: 8px 20px;
                            color: inherit;
                            padding-left: 10px;
                            padding-right: 10px;
                            text-decoration: none;
                            i {
                                padding: {
                                    left: 10px;
                                    right: 10px;
                                };
                            }
                        }
                        .fa-angle-right {
                            position: absolute;
                            right: 20px;
                            top: 11px;
                        }
                        &:hover {
                            color: #000;
                        }
                        & .child-list {
                            display: none;
                            margin-top: 10px;
                            li{
                                padding: 5px 10px;
                                margin-left: 10px;
                                margin-right: 10px;
                                font-size: 14px;
                                border-left: 4px solid #EEE;
                                transition: .3s;
                                &:hover {
                                border-left: 4px solid #000;
                                transition: 1s;
                                }
                            }
                            
                        }
                    }
                }
            }
        }
    }
}