$theme: (
    darker-theme: (bg-color: #141c2d, nav: #1f2940),
);

@each $theme, $map in $theme {
    .#{$theme} {
        .header{
            background-color: map-get($map , nav);
            .head-area {
                i {
                    color: #FFF;
                }
            }
            input {
                background-color: map-get($map , bg-color) !important;
            }
        }
        .page {
        .content-area {
            background-color: map-get($map , bg-color);
        }
        .page-head {
            .page-title {
                color: #FFF !important;
            }
            .page-desc {
                color: #FFF !important;
                opacity: 0.7;
            }
        }
        }
        .content {
        .card {
        box-shadow: none;
        }
        }

        .card {
            background-color:  map-get($map , nav);
            .card-head {
                color: #FFF;
            }
        } 

        .footer {
            background-color: map-get($map , nav) !important;
            color: #FFF;
        }

        .orders {
            .circle {
                .num {
                    color: #FFF;
                }
            }
            .details {
                .info {
                    h6, h5, span {
                        color: #FFF;
                    }
                }
            }
        }

        .detail {
            .info {
                flex: 1;
                h6 {
                   color: #EEE;;
                }
                h5 {
                    color: #FFF;
                }
            }
        }

        .coust, .sales, .country  {
            ul {
                li {
                    color: #FFF;
                    &:hover {
                        background-color: map-get($map , bg-color) !important;
                    }
                }
            }
        }
    
        .country {
            ul {
                li {
                    border-bottom: 1px solid map-get($map, bg-color) !important;
                }
            }
            .name, .number {
                color: #FFF;
            }
        }
    .table {
        border-color: red !important;
    }

    .custom-drop{
        position: relative;
        .dropdown {
            background-color: map-get($map, nav);
        }
        .setting {
            .iconx {
                i{
                    color: #000;
                }
            }
        }


    }
}

}